/* Описание */
.desk {
  margin-top: 0;
  margin-bottom: 0;
}

.desc__string {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  gap: 30px;
}

.desc__naming-string {
  color: rgb(123, 123, 123);
  font-size: 21px;
}

.desc__content-string {
  font-size: 18px;
}

.desc__hr {
  border: 3px solid rgb(158, 158, 158);
  border-radius: 10px;
}
